@import '../variables/variables';
@mixin text($fontFamily,$fontSize, $fontWeight){
  font-family:$fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  color:$bgColor5
};
@mixin text2($fontFamily,$fontSize, $fontWeight){
  font-family:$fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  color:$bgColor4
};
